<template>
    <div>
        <modal class="dup-modal" @close="hideModal" :visible="showModal">
            <p>
                {{ modalText }}
            </p>
            <div class="modal-bottom">

                <div class="buttons">
                    <div class="ui-inbox-loader" v-show="loading"></div>
                    <div v-show="!loading">
                        <button class="green" @click="resolveByAccept()">{{ modalAcceptBtnText }}</button>
                        <button class="grey" :visible="showModalCancelBtn" @click="resolveByCancel()">
                            {{ modalCancelBtnText }}
                        </button>
                    </div>
                </div>
            </div>
        </modal>

        <table class="ui-grid" :class="{ 'ui-grid-sortable': sortable }">
            <thead>
            <tr>
                <th v-for="col of columns" @click="onHeaderClick(col)">
                    {{ (col.header || col.header == '') ? col.header : col.name }}
                    <span v-if="col.sortable && sortBy == col.name"
                          :class="{ icon: true, 'icon-arrowhead-up': !sortDesc, 'icon-arrowhead-down': sortDesc }"></span>
                    <span v-else-if="col.sortable" class="icon icon-arrowhead-up-down"></span>
                </th>
            </tr>
            </thead>
            <tbody>
            <tr v-if="(!data || data.length == 0) && noDataText">
                <td :colspan="columns.length" class="center">
                    {{ noDataText }}
                </td>
            </tr>
            <tr v-for="(row, rowIndex) of sortedData" :class="processRow(row, rowIndex)"
                :key="row[keyProp] || rowIndex">
                <td v-for="(col, colIndex) of columns" :class="colClass(col, colIndex, row)">
                    <template v-for="btn in col.buttons">
                        <a :href="btn.href(row)" v-if="btn.href && !row.$loading" :target="btn.target"
                           :class="'button w-icon ' + btn.color + ' ' + btn.mark"
                           @click="onButtonClick(row, btn, $event)">
                            <span :class="btn.icon"></span>
                            {{ btn.text }}
                        </a>
                        <div v-else-if="!row.$loading"
                             :class="'button ' + (btn.text ? 'w-icon ' : '') + btn.color + ' ' + btn.mark"
                             @click="onButtonClick(row, btn, $event)">
                            <span :class="typeof btn.icon == 'function' ? btn.icon(row, col) : btn.icon"></span>
                            {{ btn.text }}
                        </div>
                    </template>
                    <div v-if="col.buttons && row.$loading" class="loader small"></div>
                    <span v-else-if="col.type == 'icon'"
                          :class="['icon', processColumn(col, row[col.name], row)]"></span>
                    <span v-else-if="col.type == 'html'" class="col-content"
                          v-html="processColumn(col, row[col.name], row)"></span>
                    <span v-else>{{ processColumn(col, row[col.name], row) }}</span>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import Modal from './modal'

export default {
    components: {
        Modal
    },
    props: {
        sortable: Boolean,
        columns: Array,
        data: Array,
        noDataText: String,
        loadSortedBy: String,
        descending: Boolean,
        rowMarker: Function,
        keyProp: String
    },
    data() {
        return {
            sortBy: null,
            sortDesc: false,
            showModal: false,
            showModalCancelBtn: false,
            modalText: "",
            modalAcceptBtnText: "",
            modalCancelBtnText: "",
            modalAcceptBtnFn: null,
            modalCancelBtnFn: null,
            modalData: null,
            loading: false
        }
    },
    computed: {
        sortedData() {
            if (!this.sortBy) return this.data;

            let column = {};
            (this.columns || []).forEach((col) => {
                if (col.name == this.sortBy) column = col;
            });

            let sort = (a, b) => {
                let avalue = a[this.sortBy];
                let bvalue = b[this.sortBy];

                if (avalue === undefined) avalue = column.value(a);
                if (bvalue === undefined) bvalue = column.value(b);

                let result = avalue == bvalue ? 0 : (avalue < bvalue ? -1 : 1);
                if (this.sortDesc) result = -result;

                return result;
            };

            let data = this.data.slice();

            if (column.sort) data.sort((a, b) => {
                return column.sort(a, b, this.sortDesc, sort);
            });
            else data.sort(sort);

            return data;
        }
    },
    methods: {
        processRow(row, index) {
            let className = index % 2 > 0 ? 'odd' : '';
            if (this.rowMarker) {
                className += ' ' + this.rowMarker(row, index);
            }
            if (row.hasOwnProperty('InstanceId')){
                className += ' instance-' + row.InstanceId;
            }
            return className.trim();
        },
        processColumn(col, value, row) {
            if (value === undefined && typeof col.value == 'function') value = col.value(row);
            if (col.format) value = col.format(value, row);

            return value;
        },
        colClass(col, index, row) {
            let value = col.name ? `col-${col.name.toLowerCase()}` : '';

            if (col.type) value += ` col-type-${col.type}`;
            if (col.buttons) value += ' col-buttons';

            if (index == 0 && col.indicate) {
                value += 'indicate ' + col.indicate(row);
            }

            if (col.mark) value += ' ' + col.mark(row);

            return value;
        },
        onButtonClick(row, btn, evt) {
            //If btn has showModal property
            if (btn.showModal) {
                if (!btn.modalCancelBtnText || btn.modalCancelBtnText == "") {
                    this.showModalCancelBtn = false;
                } else {
                    this.showModalCancelBtn = true;
                    this.modalCancelBtnText = btn.modalCancelBtnText;
                    this.modalCancelBtnFn = btn.modalCancelBtnFn;
                }
                this.modalText = btn.modalText;
                this.modalAcceptBtnText = btn.modalAcceptBtnText;
                this.modalAcceptBtnFn = btn.modalAcceptBtnFn;
                this.modalData = row;
                this.showModal = true;
            } else if (btn.click) {
                let result = btn.click(row, () => {
                    let index = this.data.indexOf(row);
                    if (index >= 0) this.$set(this.data[index], '$loading', true);
                }, () => {
                    row.$loading = false;
                });

                if (result === false) {
                    evt.preventDefault();
                    return result;
                }
            } else {
                return;
            }
        },
        onHeaderClick(col) {
            if (!col.sortable) return;

            if (this.sortBy == col.name) {
                this.sortDesc = !this.sortDesc;
            } else this.sortDesc = false;

            this.sortBy = col.name;
        },
        resolveByAccept() {
            this.modalAcceptBtnFn(this);
        },
        resolveByCancel() {
            this.modalCancelBtnFn(this);
        },
        hideModal() {
            this.resolveByCancel();
        }
    },
    created() {
        this.sortBy = this.loadSortedBy;
        this.sortDesc = this.descending || false;
    }
}
</script>


<style lang="scss">
.newui .ui-grid {
    th .icon {
        font-size: 12px !important;
        width: auto;
        height: auto;
        margin: 2px 0 0 0 !important;
    }

    .button + .button {
        margin-left: 3px;
    }
}

.uimodal {
    .modal-bottom {
        margin-top: 30px;
    }

    .buttons {
        text-align: center;
    }

    .ui-inbox-loader {
        float: none;
    }
}
</style>
