<template>
    <div class="ui-inbox-box">
        <grid v-on:refreshgrid="refreshGrid" ref="grid" :columns="columns" :data="data"></grid>
    </div>
</template>

<script>
import {description, stateName, stateEnteredAt, effectiveDate, archivedButtons} from './columns';
import Grid from '../../common/grid';

export default {
    props: {
        data: Array
    },
    components: {
        Grid
    },
    data() {
        return {
            columns: [
                description(),
                stateName(),
                stateEnteredAt(),
                effectiveDate(),
                archivedButtons()
            ]
        }
    },
    methods: {
        refreshGrid() {
            this.$emit("refreshgrid");
        }
    }
}
</script>

