<template>
    <div class="ui-inbox-box">
        <grid v-on:refreshgrid="refreshGrid" ref="grid" :columns="columns" :data="data"></grid>
    </div>
</template>

<script>
import {description, stateName, completedBy, stateEnteredAt, effectiveDate, progressButtons} from './columns';
import Grid from '../../common/grid';

let colStateEnteredAt = stateEnteredAt();
colStateEnteredAt.header = 'Completed At';

export default {
    components: {
        Grid
    },
    props: {
        data: Array
    },
    data() {
        return {
            columns: [
                description(),
                stateName(),
                completedBy(),
                colStateEnteredAt,
                effectiveDate(),
                progressButtons()
            ]
        }
    },
    methods: {
        refreshGrid() {
            this.$emit("refreshgrid");
        }
    }
}
</script>

