if (!('config' in window)) {
    window.config = {};
}

// window.config.uisession = String.uniqueID();
window.config.uiready = false;
window.config.login = null;
window.config.oldess = false;
window.config.datalist = false;
window.config.loaders = {};
window.config.loaders.dark = 'data:image/gif;base64,';
window.config.loaders.light = 'data:image/gif;base64,';
window.config.loaders.blue = 'data:image/gif;base64,';
window.config.loaders.large = {};
window.config.loaders.large.light = 'data:image/gif;base64,';
window.config.loaders.large.dark = 'data:image/gif;base64,';
window.config.agent = window.navigator.userAgent;
window.config.oldess = false;
window.config.oldessLaunched = false;
window.config.oldessFrame = false;
window.config.oldessWindow = false;
document.config = window.config
window.UI = {};

window.UI.lookups = {};
window.UI.lookupLoaders = [];

window.UI.displaylookups = {};
window.UI.displaylookupLoaders = [];

window.UI.dependancies = {};
window.UI.dependancyLoaders = [];

window.UI.autocomplete = {};

window.UI.lastAutoKill = {};
window.UI.delayAutoKill = function (uuid, killid) {
    if (document.getElement('.' + killid)) {
        document.getElement('.' + killid).removeEvents();
        document.getElement('.' + killid).destroy();
    }
    window.UI.lastAutoKill[uuid] = null;
    delete window.UI.lastAutoKill[uuid];
}
