<template>
    <tile
        title="My Details"
        icon="icon-user"
        class="cf-widget my-details-widget"
        :loading="isLoading">
        <div v-if="loadError">{{ loadError }}</div>
        <div v-else>
            <div class="field-display item" v-if="commonName">
                <div class="label">{{ commonName.Label }}</div>
                <div class="value">{{ commonName.Value }}</div>
            </div>
            <div class="field-display item" v-if="employeeNo">
                <div class="label">{{ employeeNo.Label }}</div>
                <div class="value">{{ employeeNo.Value }}</div>
            </div>
            <div class="field-display item" v-if="companyNo">
                <div class="label">{{ companyNo.Label }}</div>
                <div class="value">{{ companyNo.Value }}</div>
            </div>
            <component
                v-for="(item, index) in items"
                :is="item.Type"
                :data="item"
                class="item"
                :key="index">
            </component>
        </div>
    </tile>
</template>

<script>
// import CfWidget from "./cf-widget/index.vue";
import axios from "axios";
import Tile from "../common/tile"
import moment from "moment";
import FieldDisplay from "@/components/widgets/cf-widget/field-display";
import Separator from "@/components/widgets/cf-widget/separator-line";

export default {
    components: {
        Tile,
        FieldDisplay,
        Separator,
    },
    data() {
        return {
            department: "",
            employeeNo: "",
            companyNo: "",
            position: "",
            commonName: "",
            user: this.$user,
            isLoading: true,
            localData: [],
            loadError: null
        };
    },
    computed: {
        fullName() {
            return `${this.$user.FirstName} ${this.$user.LastName}`;
        },
        items() {
            return this.localData.filter(item => {
                return (
                    item.Type === "Separator" ||
                    item.Type === "Subheading" ||
                    item.Type === "Explanation" ||
                    item.Type === "Paragraph" ||
                    item.Type === "YoutubeInput" ||
                    item.Value
                );
            });
        }
    },
    created() {
        axios
            .post("/CleverForms/GetUserDetails")
            .then(resp => {
                if (resp.data.error) {
                    this.loadError = "Could not load personal details.";
                    return;
                }

                let toRemove = [];

                resp.data.forEach(f => {
                    switch (f.Key) {
                        case "Department":
                            this.department = f;
                            toRemove.push(f);
                            break;
                        case "EmployeeNo":
                            this.employeeNo = f;
                            toRemove.push(f);
                            break;
                        case "CompanyNo":
                            this.companyNo = f;
                            toRemove.push(f);
                            break;
                        case "CommonName":
                            this.commonName = f;
                            toRemove.push(f);
                            break;
                    }
                    if (f.InputType == "DateTime" && f.Value) {
                        f.Value = moment(f.Value).format("ddd DD MMM YYYY");
                    }
                });

                toRemove.forEach(f => {
                    let index = resp.data.indexOf(f);
                    resp.data.splice(index, 1);
                });

                // PLEASE DO NOT MODIFY "GAReady" EVENT DISPATCHERS BELOW
                // If you MUST move or remove this, you MUST dispatch "GAReady" whenever this app has an authorised user with a valid companyNumber and employeeNumber.
                // This is vital for Affinity Management Analytics reporting.
                try {
                    window.dispatchEvent(new CustomEvent('GAReady', {
                        bubbles: true,
                        detail: {companyNumber: resp.data.companyNo, employeeNumber: resp.data.employeeNo}
                    }));
                } catch (err) {
                    console.warn('GA tracking failed. Contact BenK.');
                }

                this.localData = resp.data;
            })
            .catch(() => {
                this.loadError = "Could not load personal details.";
            })
            .then(() => {
                this.isLoading = false;
            });
    }
}
</script>


<style lang="scss">
.my-details-widget {
    table {
        width: 100%;

        td + td {
            text-align: right;
        }
    }
}
</style>
