import moment from "moment";
import Vue from "vue";
import axios from "axios";

export function description() {
    return {
        name: "DisplayName",
        header: "Name",
        sortable: true,
        type: "html",
        indicate() {
            return "none";
        },
        format(value, data) {
            if (data.ShareOrDelegate) {
                if (data.InstanceKey) value = `(${data.InstanceKey}) ${value}`;
                value += `<span class="tag ${data.ShareOrDelegate.toLowerCase()}">${
                    data.SharedBy
                }</span>`;
            }

            return value;
        }
    };
}

export function stateName() {
    return {
        name: "StateName",
        header: "Status",
        sortable: true
    };
}

export function stateAssigneeName() {
    return {
        name: "StateAssigneeName",
        header: "Assigned To",
        sortable: true
    };
}

export function stateEnteredAt() {
    return {
        name: "StateEnteredAt",
        header: "Completed At",
        type: "date",
        sortable: true,
        format(value) {
            return moment(value).format("DD/MM/YYYY h:mm a");
        }
    };
}

export function completedBy() {
    return {
        name: "CompletedBy",
        header: "Completed By",
        sortable: true
    };
}

export function effectiveDate() {
    return {
        name: "EffectiveDate",
        header: "Effective Date",
        type: "date",
        sortable: false,
        format(value) {
            if (value === null || value.toString().toLowerCase().trim() === 'null' || value.toString().trim() === '') return '';
            return moment(value).format("DD/MM/YYYY");
        }
    };
}

export function viewButtons() {
    return {
        header: "",
        buttons: [
            {
                color: "blue",
                icon: "icon-page",
                text: "View",
                href(row) {
                    return "/CleverForms/ViewInstance/" + row.InstanceId;
                }
            }
        ]
    };
}

export function editButtons() {
    return {
        header: "",
        buttons: [
            {
                icon: "icon-pencil",
                text: "Edit",
                color: "blue",
                href(row) {
                    return "/CleverForms/EditInstance/" + row.InstanceId;
                }
            },
            {
                icon: "icon-box",
                text: "",
                color: "orange",
                showModal: true,
                modalAcceptBtnText: "Confirm",
                modalCancelBtnText: "Cancel",
                modalText: "Are you sure you want to archive this form?",
                modalAcceptBtnFn(target) {
                    target.loading = true;
                    axios.get("/CleverForms/ArchiveInbox/" + target.modalData.InstanceId)
                        .then(resp => {
                            if (document.querySelector('tr.instance-' + target.modalData.InstanceId))  {
                                document.querySelector('tr.instance-' + target.modalData.InstanceId).classList.add('archiving');
                            }
                            target.$emit("refreshgrid");
                            if (resp.data.success) {
                                target.showModal = false;
                                target.loading = false;
                            }
                        })
                        .catch();
                },
                modalCancelBtnFn(target) {
                    target.showModal = false;
                }
            }
        ]
    };
}


