<template>
    <tile
        v-if="showWidget"
        icon="icon-plane"
        title="My Leave"
        class="my-leave-widget"
        :loading="loading">
        <div v-if="error">{{ error }}</div>
        <div v-else class="detail-section">
            <div v-if="annual.show" class="detailRow">
                <div class="detail-value green">{{ annual.units }}</div>
                <div class="detail-description">{{ annual.unitType }} of {{ annual.title }}</div>
            </div>
            <div v-if="longservice.show" class="detailRow">
                <div class="detail-value blue">{{ longservice.units }}</div>
                <div class="detail-description">{{ longservice.unitType }} of {{ longservice.title }}</div>
            </div>
            <div v-if="sick.show" class="detailRow">
                <div class="detail-value orange">{{ sick.units }}</div>
                <div class="detail-description">{{ sick.unitType }} of {{ sick.title }}</div>
            </div>
        </div>
    </tile>
</template>

<script>
import axios from "axios";
import Tile from "../common/tile"
import moment from "moment";

class Leave {
    constructor(title, date) {
        this.title = title;
        this.date = date;
        this.show = false;
        this.units = 0;
        this.unitType = '';
    }
}

export default {
    components: {
        Tile
    },
    data() {
        return {
            loading: true,
            annual: null,
            longservice: null,
            sick: null,
            error: null
        };
    },
    methods: {
        reset() {
            this.annual = new Leave("Annual Leave");
            this.longservice = new Leave("Long Service Leave");
            this.sick = new Leave("Personal Leave");
        },
        add(leave, data) {
            leave.title = data.CodeDescription;
            leave.date = moment(data.PeriodEndDate).format('ddd DD MMM YYYY');
            leave.show = true;

            if (data.UnitType === "D") {
                leave.units = data.TotalDays;
                leave.unitType = "Days";
            } else if (data.UnitType === "H") {
                leave.units = data.TotalHours;
                leave.unitType = "Hours";
            }

            if (data.IncludeFutureLeave) {
                leave.subText = "inc. future leave";
                
            } else {
                leave.subText = "exc. future leave";
            }
        },
        floor(value) {
            return Math.floor(value);
        }
    },
    computed: {
        showWidget() {
            return (
                this.loading ||
                this.annual.show ||
                this.sick.show ||
                this.longservice.show
            );
        }
    },
    created() {
        this.reset();

        axios
            .post("/Leave/GetBalances")
            .then(resp => {
                if (resp.data.error) {
                    return console.log(data.error);
                }

                let data = resp.data.Data;

                this.reset();

                if (data && data.ComponentBalances) {
                    data.ComponentBalances.forEach(compBal => {
                        compBal.CodeBalances.forEach(codeBal => {
                            switch (codeBal.LeaveCode) {
                                case "07":
                                    this.add(this.sick, codeBal);
                                    break;
                                case "09":
                                    this.add(this.annual, codeBal);
                                    break;
                                case "10":
                                    this.add(this.longservice, codeBal);
                                    break;
                            }
                        });
                    });
                }
            })
            .catch(err => {
                this.error = "Could not load leave balances.";
            })
            .then(() => {
                this.loading = false;
            });
    }
};
</script>


<style lang="scss">
$leave-tile-width: 70px;
$button-blue: #44b5ec;
$button-green: #7abd2d;
$button-yellow: #ffbd00;
$button-orange: #ff6600;

.my-leave-widget {
    .detail-section > .detailRow:first-child {
        margin-top: 0;
    }
    .detailRow {
        clear: both;
        float: none;
        margin: 20px 0 5px 0;
        color: #666;
    }

    .detailRow .detail-value {
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        width: auto;
        min-width: 70px;
        font-size: 20px;
    }

    .detailRow .detail-description {
        display: inline-block;
        vertical-align: middle;
        font-size: 16px;
        margin-left: 10px;
    }

    .green {
        color: lighten($button-green, 10%);
    }

    .blue {
        color: lighten($button-blue, 10%);
    }

    .orange {
        color: lighten($button-orange, 10%);
    }


    .detailRow .numTile {
        position: relative;
        float: left;
        width: $leave-tile-width;
        height: 60px;
        background-color: #fff;
        border-radius: 5px;
    }

    .detailRow .numTile .style {
        position: absolute;
        top: 29px;
        width: $leave-tile-width;
        height: 28px;
        border-top: 2px solid #f3f3f3;
    }

    .detailRow .numTile .value {
        position: absolute;
        top: 0;
        width: $leave-tile-width;
        height: 60px;
        line-height: 60px;
        font-size: 30px;
        text-align: center;
        letter-spacing: -3px;
    }

    .detailRow .numValue {
        margin-left: 6%;
        margin-top: 24px;
        width: 280px;
    }

    .detailRow .numValue .title {
        margin: 0;
        font-size: 16px;
        font-weight: normal;
        white-space: nowrap;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .detailRow .numValue .hours {
        font-size: 12px;
        margin-top: 5px;
    }

    .detailRow .entitlement {
        float: right;
        width: 30%;
        /*margin: 15px 0 0 0;*/
        font-size: 12px;
        text-align: right;
        /*line-height: 20px;*/
    }

    .detailRow.blue .numTile .value {
        color: lighten($button-blue, 10%);
    }

    .detailRow.green .numTile .value {
        color: lighten($button-green, 10%);
    }

    .detailRow.orange .numTile .value {
        color: lighten($button-orange, 10%);
    }
}
</style>
