<template>
    <cf-widget
        title="My Manager"
        icon="icon-user"
        class="my-manager-widget"
        :data="data"
        :loading="loading"
        :error="error">
    </cf-widget>
</template>

<script>
import CfWidget from "./cf-widget/index.vue";
import axios from "axios";

export default {
    data() {
        return {
            data: null,
            loading: true,
            error: null
        };
    },
    created() {
        axios
            .post("/CleverForms/GetManagerDetails")
            .then(resp => {
                if (resp.data.error) {
                    return console.log(resp.data.error);
                }

                this.data = resp.data;
            })
            .catch(() => {
                this.error = "Could not load manager's details.";
            })
            .then(() => {
                this.loading = false;
            });
    },
    components: {
        CfWidget
    }
};
</script>

<style lang="scss">
.my-manager-widget {
    table {
        width: 100%;

        td + td {
            text-align: right;
        }
    }
}
</style>
