<template>
    <transition name="appear">
        <div class="uimodalbox" v-if="visible">
            <div class="uimodalbg white"></div>
            <div class="uimodalcontainer">
                <div class="whitespace" @mousedown="close"></div>
                <div class="uimodal shadow">
                    <div class="uimodalclose" v-show="showCloseButton" @click="close">
                        <span class="smicon cross"></span>
                    </div>
                    <div class="uimodalbody">
                        <slot></slot>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    props: {
        visible: Boolean,
        showCloseButton: Boolean
    },
    methods: {
        close() {
            this.$emit('close');
        }
    }
}
</script>


<style lang="scss">
.uimodalbox {
    display: table;
    top: 0;
    left: 0;
    opacity: 1;
    transition: opacity .2s linear;

    &.appear-enter,
    &.appear-leave-active {
        opacity: 0;
    }

    .uimodalcontainer {
        display: table-cell;
        text-align: center;
        width: 100%;
        position: fixed;
        z-index: 999999;
        overflow-y: auto;
        padding: 10px 0;
        max-height: 100%;
        box-sizing: border-box;
        height: 100%;

        .whitespace {
            width: 100%;
            // height: 100%;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
        }
    }

    .uimodal {
        position: relative;
        display: inline-block;
        text-align: left;
        top: 0;
    }
}
</style>
